.About-know {
  -webkit-transform: skew(-8deg);
      -ms-transform: skew(-8deg);
          transform: skew(-8deg);
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  background-color: #fcfcfc;
  color: #4c4c4c;
}

.About-normal {
  -webkit-transform: skew(8deg);
      -ms-transform: skew(8deg);
          transform: skew(8deg);
  width: 85%;
  margin: 0 auto;
}

.About-list  {
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
}

.About-item {
  font-size: 18px;
  margin-top: 5px;
}



